







import Vue from "vue";
import Navi from "@/components/Navi.vue";
import { Store } from "@/store";

const navlist = [
  { name: "home", path: "/" },
  { name: "playlist", path: "/playlist" },
  { name: "player", path: "/player" }
];

export default Vue.extend({
  name: "app",
  props: {
    store: {
      type: Store
    }
  },
  data() {
    return {
      navlist: navlist
    };
  },
  components: {
    Navi
  }
});



